import { ref } from "vue";

export default function () {
    let reciveArray = [];
    let fileName = ref();
    let webrctMessage = ref();
  
    let onMesRTC = (e) => {if (typeof(e.data) == 'string'){
        const received = new Blob(reciveArray);   
        webrctMessage.value =  URL.createObjectURL(received);
        fileName.value = JSON.parse(e.data).name;
        reciveArray = []
      } else{
        reciveArray.push(e.data);}
    }   
  return {
    fileName,
    webrctMessage,
    onMesRTC
  }
  
    
  
}
